body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  text-align: center;
}


.post {
  display: flex;
  flex-direction: row;
  background: #5580a3;
  max-width: 600px;
  padding: 20px;
  border-radius: 6px;
  border: 3px outset #acacac;
  color: #fff;
  margin: 20px auto;
}

.post__header {
  display: flex;
}

.post__logo img {
  border-radius: 50px;
  width: 60px;
}

.post__description {
  display: flex;
  flex-direction: column;
  margin: auto;
  padding: 0px 15px;
}

.post__description img {
  width: 100%;
  border-radius: 15px;
}

.post__description span {
  padding: 13px 8px;
  text-align: left;
  
}

.post-add__form {
  display: flex;
  flex-direction: column;
  margin: auto;
  max-width: 350px;
  padding: 20px;
}

input, select{
  padding: 6PX;
  margin: 10px 0;
}
